import React from "react";
import Grid from "@mui/material/Grid";

export default function ImgContainer(props) {
  const { margin, images, justify, grid } = props;

  return (
    <Grid
      container
      className="imgSort"
      style={{
        margin: `${margin}px auto`,
        justifyContent: justify,
      }}
    >
      {images.map((obj, i) => (
        <Grid
          item
          xs={12}
          lg={grid}
          key={i}
          className={`${grid === 3 ? "gridSpread" : "gridCenter"}`}
        >
          <a href={obj.link} target="_blank" rel="noreferrer">
            <img src={obj.img} width={obj.width} alt={`${obj.img}`} />
          </a>
        </Grid>
      ))}
    </Grid>
  );
}
